import React, { useState, useEffect } from "react"
import { useDispatch, shallowEqual, useSelector } from "react-redux"
import { useStaticQuery, graphql } from "gatsby"

import Property from "./components/Property"
import Pagination from "./components/Pagination"
import {
  fetchPropertyList,
  changePageNumber,
} from "../../../redux/actions/propertyListActions"

const ListingSection = () => {
  const dispatch = useDispatch()
  const data = useStaticQuery(graphql`
    query Properties {
      wordpress {
        properties {
          nodes {
            PropertyOptions {
              homefeatured
            }
            title
            slug
            PropertyBasics {
              status
              price
              status
              bedrooms
              bathrooms
            }
            PropertyImages {
              mainimage {
                sourceUrl
                altText
              }
              gallery {
                sourceUrl
                altText
              }
            }
            PropertyAgent {
              agent {
                ... on WORDPRESS_Agent {
                  id
                  Agent {
                    name
                  }
                  featuredImage {
                    mediaItemUrl
                  }
                }
              }
            }
            PropertyLocations {
              fieldGroupName
            }
          }
        }
      }
    }
  `)
  console.error("DATADATA", data)
  const [propertyPerPage, setPropertyPerPage] = useState(4)

  const properties = data.wordpress.properties.nodes
  const pageNumber = useSelector(
    state => state.propertyListReducer.pageNumber,
    shallowEqual
  )
  const numberOfProperties = useSelector(
    state => state.propertyListReducer.numberOfProperties,
    shallowEqual
  )

  useEffect(() => {
    if (properties[pageNumber].length < 1) {
      dispatch(fetchPropertyList(pageNumber, propertyPerPage))
    }
  }, [])

  const handlePagination = page => {
    if (page === "next") {
      if (!Object.keys(properties).includes(String(pageNumber + 1))) {
        dispatch(fetchPropertyList(pageNumber + 1, propertyPerPage, false))
      } else {
        dispatch(changePageNumber(pageNumber + 1))
      }
    } else if (page === "previous") {
      if (!Object.keys(properties).includes(String(pageNumber - 1))) {
        dispatch(fetchPropertyList(pageNumber - 1, propertyPerPage, false))
      } else {
        dispatch(changePageNumber(pageNumber - 1))
      }
    } else {
      if (!Object.keys(properties).includes(String(page))) {
        dispatch(fetchPropertyList(page, propertyPerPage, false))
      } else {
        dispatch(changePageNumber(page))
      }
    }
  }

  return (
    <section>
      <div className="container">
        <div className="row">
          <div className="col-lg-12 col-sm-12 list-layout">
            <div className="row">
              <div className="col-lg-12 col-md-12">
                <div className="filter-fl">
                  <h4>
                    Total Property Find is:{" "}
                    <span className="theme-cl">{numberOfProperties}</span>
                  </h4>
                  <div className="btn-group custom-drop">
                    <button
                      type="button"
                      className="btn btn-order-by-filt"
                      data-toggle="dropdown"
                      aria-haspopup="true"
                      aria-expanded="false"
                    >
                      Short By<i className="ti-angle-down"></i>
                    </button>
                    <div className="dropdown-menu pull-right animated flipInX">
                      <a href="#">Latest</a>
                      <a href="#">Most View</a>
                      <a href="#">Most Popular</a>
                    </div>
                  </div>
                </div>
              </div>

              {/* Single Property Start */}
              {properties.map((propert, index) => {
                return <Property key={index} {...propert} />
              })}
              {/* Single Property Ends */}
            </div>

            {/* Pagination STARTS*/}
            {numberOfProperties > 0 && (
              <Pagination
                count={numberOfProperties}
                perPage={propertyPerPage}
                handlePagination={handlePagination}
                pageNumber={pageNumber}
              />
            )}
            {/* Pagination ENDS*/}
          </div>
        </div>
      </div>
    </section>
  )
}

export default ListingSection
