import React from "react"
import { Link } from "gatsby"
import { FormattedNumber } from "react-intl"
import { link } from "../../../../App"

const Property = ({
  title,
  slug,
  PropertyBasics,
  PropertyImages,
  PropertyAgent,
  PropertyLocations,
}) => {
  return (
    <div className="col-lg-12 col-md-12">
      <div className="property-listing property-1">
        <div className="listing-img-wrapper">
          <Link to={"/property/" + slug}>
            <img
              src={`${PropertyImages.mainimage.sourceUrl}`}
              className="img-fluid mx-auto"
              alt=""
            />
          </Link>
          <div className="listing-like-top">
            <i className="ti-heart"></i>
          </div>
          <div className="listing-rating">
            <i className="ti-star filled"></i>
            <i className="ti-star filled"></i>
            <i className="ti-star filled"></i>
            <i className="ti-star filled"></i>
            <i className="ti-star"></i>
          </div>
          <span className="property-type">For {PropertyBasics.status}</span>
        </div>

        <div className="listing-content">
          <div className="listing-detail-wrapper">
            <div className="listing-short-detail">
              <h4 className="listing-name">
                <Link to={"/property/" + slug}>{title}</Link>
              </h4>
              <span className="listing-location">
                <i className="ti-location-pin"></i>
                {PropertyLocations.fieldGroupName}
              </span>
            </div>
            <div className="list-author">
              <Link to={"/property/" + slug}>
                <img
                  src={
                    PropertyAgent.agent.length > 0
                      ? `${PropertyAgent.agent[0].featuredImage.mediaItemUrl}`
                      : `${link}/img/users/default.jpg`
                  }
                  className="img-fluid img-circle avater-30"
                  alt=""
                />
              </Link>
            </div>
          </div>

          <div className="listing-features-info">
            <ul>
              <li>
                <strong>Bed:</strong>
                {PropertyBasics.bedrooms}
              </li>
              <li>
                <strong>Bath:</strong>
                {PropertyBasics.bathrooms}
              </li>
              <li>
                <strong>Sqft:</strong>
                {PropertyBasics.sqft}
              </li>
            </ul>
          </div>

          <div className="listing-footer-wrapper">
            <div className="listing-price">
              <h4 className="list-pr">
                <FormattedNumber
                  value={PropertyBasics.price}
                  style="currency"
                  currency="CAD"
                />
              </h4>
            </div>
            <div className="listing-detail-btn">
              <Link to={"/property/" + slug} className="more-btn">
                More Info
              </Link>
            </div>
          </div>
        </div>
      </div>
    </div>
  )
}

export default Property
